import React, { Component } from 'react';
import './Masthead.css';


export default class Element extends Component {

    componentDidMount() {
        require('../../js/external/twitter.js')();
    }

    render() {

        return (
            <div className="Masthead">
                <div className="Masthead__item">
                    <span className="Masthead__label white">Conçu, écrit et réalisé par</span>
                    <span className="Masthead__details">Lucie Berbey, Louise-Adélaïde Boisnard, Valentin Boulay, Agathe Boussard, Hippolyte Corneille, Charlie Courrent, Louise Gerber, Hugo Lallier, Sarah Meyssonnier, Marion Michel.</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Graphiste</span>
                    <span className="Masthead__details">Léo Dupuy</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Illustratrice</span>
                    <span className="Masthead__details">Julie Lassy</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Photographe</span>
                    <span className="Masthead__details">Sarah Meyssonnier</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Vidéo</span>
                    <span className="Masthead__details">Hippolyte Corneille (avec archives de <i>La chaîne L'Équipe</i>)</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Développeuse</span>
                    <span className="Masthead__details">Marion Michel</span>
                </div>
                <div className="Masthead__item">
                    <span className="Masthead__label white">Remerciements</span>
                    <span className="Masthead__details">Fallon Sherrock, Barry Hearn (président de la PDC), Dave Allen (attaché de presse de la PDC), Rod Studd, Laura Turner, Russ Bray, Thibault Tricole, Carole Frison, Swann Borsellino, Michael van Gerwen, Matthew Kiernan, Gary Horsley, l'École Supérieure de Journalisme de Lille, le BDE de l'ESJ Lille, Habib Hadjazi et Martin.</span>
                    </div>
            </div>
        );
    }
}



